<template>
<div class="cs_fwb" v-if="is_show">
    <div class="cs-head">
        <span class="el-dialog__title ha-right-dialog__title" ></span>
    </div>
    <div class="el-dialog__body  ha-dialog-right__body">
        <el-input v-model="input" placeholder="">{{parmas.title}}</el-input>
        <quill-editor ref="myTextEditor" v-model="content" :config="editorOption"></quill-editor>
    </div>
    <div class="el-dialog__footer">
        <div class="dialog-footer">
            <button type="button" class="el-button el-button--default" @click="deletclick($event)">{{parmas.btn01}}</button>
            <button type="button" class="el-button el-button--primary" @click="deletclick($event)">{{parmas.btn02}}</button>
            <button type="button" class="el-button el-button--primary" @click="deletclick($event)">{{parmas.btn03}}</button>
        </div>
    </div>
    <tankuangcomponent v-if="is_tkshow" :content="content"></tankuangcomponent>
</div>
</template>


<script>
    import { quillEditor } from 'vue-quill-editor';
    export default {
        props:{
            parmas:{
                type:Object,
                default:function(){
                    return {
                        title:"",
                        message:"",
                        btn01:"",
                        btn02:"",
                        btn03:"",

                    }
                }
            },
            is_show:{
                type:Boolean,
                default:false,
            }
        },
        data: function(){
            return {
                input:"",
                content: '<p>Hello BBK</p>',
                editorOption: {
                    // something config
                },
                is_tkshow:false,
                content:{},
            }
        },
        components: {
            quillEditor,
            tankuangcomponent:function (resolve, reject) {
                require(['./tankuang.vue'],  resolve)
            }
        },
        methods: {
            deletclick(e){
                if(e.target.innerText==""){
                    this.shanchu();
                }
                if(e.target.innerText==""){
                    this.chexia();
                }
                if(e.target.innerText==""){
                    this.fabu();
                }
                if(e.target.innerText==""){
                    this.bianji();
                }
            },
            bianji(){
                this.is_tkshow=true;
                this.content={
                    button:"",
                    message:"",
                    fn:function(){
                        alert("hehe")
                    }
                }
            },
            shanchu(){
                this.is_tkshow=true;
                this.content={
                    button:"",
                    message:"",
                    fn:function(){
                        alert("hehe")
                    }
                }
            },
            chexia(){
                this.is_tkshow=true;
                this.content={
                    button:"",
                    message:"",
                    fn:function(){
                        alert("hehe")
                    }
                }

            },
            fabu(){
                this.is_tkshow=true;
                this.content={
                    button:"",
                    message:"",
                    fn:function(){
                        alert("hehe")
                    }
                }
            }
        },
        created(){
            var that=this;
            this.content=this.parmas.message;
            this.input=this.parmas.title; 
            var Bus=global.eventHub
            Bus.$on("tankuang",function(){
                 that.is_tkshow=false;
            });
        },
        computed: {
            editor() {
                return this.$refs.myTextEditor.quillEditor;
            }
        }
    }
</script>
<style scoped>
    .cs-head{
        height: 40px;
        background: #eef1f6;
        padding-left: 15px;
        line-height: 40px;
    }
    .cs_fwb{
        background: #fff;
    }
    .el-dialog__body .el-input{
        margin-bottom: 20px;
    }
    .quill-editor{
        height: 200px;
    }
</style>


